import Device from "@/pure/Device";

import {
  Scene,
  WebGLRenderer,
  // Color,
  PerspectiveCamera,
  SRGBColorSpace,
  ACESFilmicToneMapping,
  Raycaster,
} from "three";

// import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { getFovHeigth } from "@trinketmage/sword";
import { Pane } from "tweakpane";
import * as CamerakitPlugin from '@tweakpane/plugin-camerakit';

class Common {
  scene = new Scene();
  handle = document.body.querySelector('#graphics');
  raycaster = new Raycaster();
  debug = null;

  PARAMS = {
    fov: 50,
    pedestal: 0,
    truck: 0,
    dolly: 10,
    pan: 0,
    tilt: 0,
    roll: 0,
  };

  constructor() {
    const { PARAMS } = this;
    // this.scene.background = new Color(0x030c1e);
    this.camera = new PerspectiveCamera(
      PARAMS.fov,
      Device.viewport.width / Device.viewport.height,
      0.01,
      100.0
    );
    this.camera.position.z = PARAMS.dolly;
  }

  init() {
    this.renderer = new WebGLRenderer({
      // canvas: canvas,
      alpha: true,
      // stencil: false,
      // depth: true,
      powerPreference: "high-performance",
      antialias: false
    });
    this.handle.appendChild( this.renderer.domElement );

    this.renderer.autoClear = false;
    this.renderer.physicallyCorrectLights = true;
    // this.renderer.outputColorSpace = LinearSRGBColorSpace;
    this.renderer.outputColorSpace = SRGBColorSpace;
    // this.renderer.colorSpace = LinearSRGBColorSpace;
    // ACESFilmicToneMapping;
    this.renderer.toneMapping = ACESFilmicToneMapping;
    // this.renderer.setClearColor( 0xffffff, 0 ); // the default


    this.renderer.setPixelRatio(Device.pixelRatio);

    if (window.location.hash === '#debug') {
      this.debug = new Pane();
      this.debug.registerPlugin(CamerakitPlugin);
    }
    // this.controls = new OrbitControls(this.camera, this.renderer.domElement);
  }

  render() {
    this.renderer.render(this.scene, this.camera);
  }

  dispose() {
    this.handle.removeChild( this.renderer.domElement );
    this.renderer.dispose();
    this.debug?.dispose();
  }

  resize() {
    Device.viewport.width = this.renderer.domElement.parentElement.offsetWidth;
    Device.viewport.height = this.renderer.domElement.parentElement.offsetHeight;

    this.camera.aspect =
      Device.viewport.width / Device.viewport.height;
    this.camera.updateProjectionMatrix();

    Device.fov.height = getFovHeigth(this.camera, 10);
    Device.fov.width = Device.fov.height * this.camera.aspect;

    this.renderer.setSize(
      Device.viewport.width,
      Device.viewport.height
    );
  }
  setDebug() {
    const { PARAMS } = this;
    const folder = this.debug.addFolder({
      title: '🎥 Camera',
      expanded: false
    });
    folder.addBinding(PARAMS, 'fov', {
      view: 'cameraring',
      series: 0,
      unit: {ticks: 5, pixels: 40, value: 10},
      min: 1,
      max: 180,
      step: 0.01,
    })
    .on('change', () => {
      this.camera.fov = PARAMS.fov;
      this.camera.updateProjectionMatrix();
    });
    const movements = folder.addFolder({
      title: 'movements',
      expanded: true
    });
    const positionable = {
      view: 'cameraring',
      series: 0,
      unit: {ticks: 5, pixels: 40, value: 5},
      min: -180,
      max: 180,
      step: 0.01,
      wide: true,
    }
    movements.addBinding(PARAMS, 'truck', {
      ...positionable
    })
    .on('change', () => {
      this.camera.position.x = PARAMS.truck;
      this.camera.updateProjectionMatrix();
    });
    movements.addBinding(PARAMS, 'pedestal', {
      ...positionable
    })
    .on('change', () => {
      this.camera.position.y = PARAMS.pedestal;
      this.camera.updateProjectionMatrix();
    });
    movements.addBinding(PARAMS, 'dolly', {
      ...positionable
    })
    .on('change', () => {
      this.camera.position.z = PARAMS.dolly;
      this.camera.updateProjectionMatrix();
    });
    const rotable = {
      view: 'cameraring',
      series: 0,
      unit: {ticks: 5, pixels: 100, value: 5},
      min: -Math.PI,
      max: Math.PI,
      step: 0.01,
      wide: true,
    }
    movements.addBinding(PARAMS, 'pan', {
      ...rotable
    })
    .on('change', () => {
      this.camera.rotation.y = PARAMS.pan;
      this.camera.updateProjectionMatrix();
    });
    movements.addBinding(PARAMS, 'tilt', {
      ...rotable
    })
    .on('change', () => {
      this.camera.rotation.x = PARAMS.tilt;
      this.camera.updateProjectionMatrix();
    });
    movements.addBinding(PARAMS, 'roll', {
      ...rotable
    })
    .on('change', () => {
      this.camera.rotation.z = PARAMS.roll;
      this.camera.updateProjectionMatrix();
    });

  }
}

export default new Common();
